
* {
  outline: 0px;
}
body {
  overflow-y: scroll;
  overflow-x: none;
}
.clear {
  clear: both;
}
.hidden {
  display: none;
}
.inactive {
  opacity: 0.5;
}
a, a:hover, a:active, a:visited {
  color: inherit;
}
